import LazyHero from 'react-lazy-hero';
import LayoutSectionBody from "@components/layout/section/common/body.js";
import PostHeroText from "@components/layout/section/common/blog/postHeroText.js";
import LayoutHeader from "@components/layout/section/common/layoutHeader.js";
import PageHeader from "@components/layout/header/header.js";
import PageHeaderLinks from "@components/layout/header/headerLinks.js";
import PageFooter from "@components/layout/footer/footer.js";
import BlogSideNav from "@components/layout/blog/sideNav/side.js";

// core components
import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";
import Parallax from "@components/Parallax/Parallax.js";

import { SnackbarProvider } from 'notistack';

import SEO from "@components/seo/seo"

import { layoutPageStyle } from "assets/jss/views/layout/layout.js";
import { globalPageStyle } from "assets/jss/common/globalPage.js"
import { layoutPostStyle } from "assets/jss/template/blog/post.js";

library.add(fab);
const dashboardRoutes = [];

const useStyles = makeStyles(layoutPageStyle);
const useStylesPage = makeStyles(globalPageStyle);
const useStylesPost = makeStyles(layoutPostStyle);


LayoutBlog.propTypes = {
  children: PropTypes.node.isRequired,
  pageNode: PropTypes.object.isRequired,
  props: PropTypes.object,
}

