function GetHeroClassNames(pageNode, classes) {
  const heroClassNames = [classes.hero];
  if (pageNode.context.pageStyle === null) {
    return (heroClassNames)
  }

  if (pageNode.context.pageStyle[0].name === "hero") {
    if (pageNode.context.pageStyle[0].style === null) {
      return (heroClassNames)
    }
    return (heroClassNames.concat(pageNode.context.pageStyle[0].style));
  }
  for (let index = 0; index < pageNode.pageStyle.length; index++) {
    if (pageNode.context.pageStyle[index].name === "hero") {
      return (heroClassNames.concat(pageNode.context.pageStyle[index].style));
    }
  }

  return heroClassNames

}

export default function LayoutBlog({ children, pageNode, ...props }) {

  const classes = useStyles();
  const classesPage = useStylesPage();
  const classesPost = useStylesPost();

  const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";  
  if (typeof window !== 'undefined'){
    localStorage.setItem("themeVariation", themeVariation);
  }

 

  return (
    <SnackbarProvider
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
    >
      <LayoutHeader classesPage={classesPage} />
      <SEO pageNode={pageNode} />


      <section className="header">
        <PageHeader
          color="transparent"
          routes={dashboardRoutes}
          brand="3 Mystic Apes LLC"
          rightLinks={<PageHeaderLinks pageNode={pageNode} />}
          fixed
          changeColorOnScroll={{
            height: 15,
            color: "dark"
          }}
        />
        <Parallax filter image={require("assets/image/background/space.jpg")} pageClass={classesPage["parallaxPage"]}>
        </Parallax>
      </section>
      
      <LayoutSectionBody classes={classes} classesPage={classesPage} pageNode={pageNode}>
        <LazyHero className={classNames(GetHeroClassNames(pageNode, classesPost))} imageSrc={pageNode.context.hero === null ? null : pageNode.context.hero.publicURL}>
          <div className={`heroText`}>
            <PostHeroText classes={classes} classesPage={classesPage} classesPost={classesPost} pageNode={pageNode}/>
          </div>
        </LazyHero>
        <GridContainer justify="center">
          <GridItem xs={12} sm={12} md={12}>
            <GridContainer justify="center">
              <main className={classNames([classesPage.mainContent, classesPost.mainContent])}>
                <div>
                    {pageNode.readingTime.text}
                    <br/>
                     <div>{Moment(pageNode.context.startDate).format('MMMM DD, YYYY')}</div>
                </div>
                {children}
              </main>
              <BlogSideNav  pageNode={pageNode} classes={classes} classesPage={classesPage} classesPost={classesPost}/>
            </GridContainer>
          </GridItem>
        </GridContainer>
      </LayoutSectionBody>

      <section className="footer">
        <PageFooter />
      </section>
    </SnackbarProvider>
  )
}