import React from "react"
import { Helmet } from "react-helmet"
import PropTypes from "prop-types";
import classNames from "classnames";

import { useStaticQuery, graphql } from "gatsby"


LayoutHeader.propTypes = {
    props: PropTypes.object,
}

//GTag Code
// (function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
// new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
// j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
// 'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
// })(window,document,'script','dataLayer','GTM-WF2NJHR');



export default function LayoutHeader({ children, pageNode, ...props }) {
    const { site } = useStaticQuery(
        graphql`
      query {
        site {
          siteMetadata {
            defaultThemeName
            componentKeys {
              google {
                analytics
                adSense
              }
            }
          }
        }
      }
    `
    )
    const { classesPage } = props;    
    const themeVariation = (typeof window !== 'undefined') ? (localStorage.getItem('themeVariation') ? localStorage.getItem('themeVariation') : "dark") : "dark";  

    const bodyClassStyles = [classesPage.page, themeVariation];

    return (
        <Helmet htmlAttributes={{ class: classNames([classesPage.htmlRoot, themeVariation]) }}>
            <script async src={`https://www.googletagmanager.com/gtag/js?id=${site.siteMetadata.componentKeys.google.analytics}`} />
            <script>
                {`
          window.dataLayer = window.dataLayer || [];
          function gtag(){dataLayer.push(arguments);}
          gtag('js', new Date());
        
          gtag('config', '${site.siteMetadata.componentKeys.google.analytics}', { 'anonymize_ip': true });
          `}
            </script>
            <script data-ad-client={site.siteMetadata.componentKeys.google.adSense} async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js" />
            <body className={classNames(bodyClassStyles)} />
        </Helmet>
    )
}