import React from "react"
import { graphql, Link } from "gatsby"

import { MDXProvider } from "@mdx-js/react"
import { MDXRenderer } from "gatsby-plugin-mdx"



import LayoutBlog from "@components/layout/layoutBlog"

// MDX Components
import { H1, H2, H3, H4, H5, H6 } from "@components/header/header.js";
import Button from "@components/customButtons/button.js";
import GridContainer from "@components/Grid/GridContainer.js";
import GridItem from "@components/Grid/GridItem.js";

// Provide common components here
const shortcodes = { GridContainer, GridItem, Button, Link, H1, H2, H3, H4, H5, H6 }

/*
children: 
location: {hash:, host:, hostname:, href, key, orgin, pathname, port, protocol, search, state:{key:}}
navigate:  navigate(to, options)
pageContext: {slug: }
pageResources: {json: {}, page: {}, staticQueryResults: {}, component: }
params: {}
path: "/about"
pageContext: {slug: "/about"} //Based off gatsby-node create page
uri: "/about"
*/
export default function TemplatePost({ data, pageContext }) {
  
  const { node } = data  

  return (
    <LayoutBlog pageNode={node}>
      <MDXProvider components={shortcodes}>
        <MDXRenderer pageNode={node}>{pageContext.body}</MDXRenderer>
      </MDXProvider>
    </LayoutBlog>
  )
}

// node:threeMysticApesPost(post: {slug: {eq: $rawSlug}}) {
export const query = graphql`
 query($postID: String!) {
    node:threeMysticApesPost(id: {eq: $postID}) {
      id
      postHash
      slug
      readingTime {
        text
        minutes
        time
        words
      }
      context: post {
        title
        description
        keywords
        hashTag
        meta {
          content
          name
          property
        }
        mysticApes
        series {
          name
          part
        }
        pageStyle {
          name
          style
        }
        hero {
          publicURL
        }
        startDate
        stylePath{
          relativePath
        }
      }
      seriesNode {
        id
        slug
        post {
          title
          startDate
          ariaLabel
          excerpt
          series {
            name
            part
          }
        }
      }      
      similarNodeByTag {
        id
        slug
        post {
          title
          startDate
          ariaLabel
          excerpt
        }
      }      
      similarNodeByMysticApes {
        id
        slug
        post {
          title
          startDate
          ariaLabel
          excerpt
        }
      }
    }
  }
`